export default {
  name: 'Appheader',
  components: {
    MenuLinks: () => import('./MenuLinks.vue'),
  },
  data() {
    return {
      scrollPosition: null,
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll)
  },
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY
    },
    goUp() {
      console.log('goup')
      const div = document.querySelector('#header')
      if (div) {
        div.scrollIntoView()
      }
    },
  },
}
